import axios from "../axios";
import React, { useEffect, useState } from "react";
import { Col, Row } from "react-bootstrap";
import { LinkContainer } from "react-router-bootstrap";
import { Link } from "react-router-dom";
import categories from "../categories";
import "./Home.css";
import { useDispatch} from "react-redux";
import { updateProducts } from "../features/productSlice";
import ProductPreview from "../components/ProductPreview";

function Home() {
    const dispatch = useDispatch();
    const [products, setProducts] = useState([]);
    const lastProducts = products.slice(0, 8);

    useEffect(() => {
        axios.get("/products").then(({ data }) =>         
        {    
        dispatch(updateProducts(data))
        // console.log(data);
        setProducts(data);

    });
    }, []);
    return (
        <div>
            <img src=" https://res.cloudinary.com/learn-code-10/image/upload/v1653947013/yqajnhqf7usk56zkwqi5.png" className="home-banner" />
            <div className="featured-products-container container mt-4">
                <h2>Produits Recents</h2>
                {/* last products here */}
                <div className="d-flex justify-content-center flex-wrap">
                    
                    {lastProducts.map((product) =>{
                        return <ProductPreview key={product._id} {...product} />})
                    
                    }
                </div>
                <div>
                    <Link to="/category/all" style={{ textAlign: "right", display: "block", textDecoration: "none" }}>
                        See more {">>"}
                    </Link>
                </div>
            </div>
            {/* les categories */}
            
            <div className="recent-products-container container mt-4">
                <h2>Categories</h2>
                <Row>
                    {categories.map((category) => (
                        <LinkContainer key={category.name} to={`/category/${category.name.toLocaleLowerCase()}`}>
                            <Col md={4}>
                                <div style={{ backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(${category.img})`, gap: "10px" }} className="category-tile">
                                    {category.name}
                                </div>
                            </Col>
                        </LinkContainer>
                    ))}
                </Row>
            </div>
        </div>
    );
}

export default Home;
